<template>
  <form
    class="form-horizontal auth-form"
    @submit.prevent="handleSubmit"
    method="post"
  >
    <div class="form-group">
      <input
        v-model="form.MA_DONVI"
        type="text"
        class="form-control"
        id="exampleInputEmail1"
      />
    </div>
    <div class="form-group">
      <input type="text" v-model="form.MS_THUE" class="form-control" />
    </div>
    <div class="form-group">
      <input type="text" v-model="form.SDT_NGUOI_DD" class="form-control" />
    </div>
    <div class="form-terms">
      <div class="custom-control custom-checkbox mr-sm-2">
        <input
          type="checkbox"
          class="custom-control-input"
          id="customControlAutosizing"
        />
        <label class="custom-control-label" for="customControlAutosizing"
          >Nhớ đăng nhập</label
        >
        <a href="#" class="btn btn-default forgot-pass">Quên mật khẩu</a>
      </div>
    </div>
    <div class="form-button">
      <button class="btn btn-primary" type="submit" @click="login">
        Đăng nhập
      </button>
    </div>
  </form>
</template>

<script>
import axios from "axios";
export default {
  components: {},
  data() {
    return {
      type: "password",
      submitted: false,
      form: {
        MA_DONVI: null,
        MS_THUE: null,
        SDT_NGUOI_DD: null,
      },
      message: "",
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    },
  },
  created() {
    if (this.loggedIn) {
      this.$router.push("/dashboard/default");
    }
  },

  methods: {
    handleSubmit() {
      this.submitted = true;
    },
    async login() {
      if (
        !this.form.MA_DONVI &&
        !this.form.MS_THUE &&
        !this.form.SDT_NGUOI_DD
      ) {
        const Toast = this.$swal.mixin({
          toast: true,
          position: "top-end",
          showConfirmButton: false,
          timer: 3000,
          timerProgressBar: true,
          didOpen: (toast) => {
            toast.addEventListener("mouseenter", this.$swal.stopTimer);
            toast.addEventListener("mouseleave", this.$swal.resumeTimer);
          },
        });

        Toast.fire({
          icon: "warning",
          title: "Bạn không thể đăng nhập nếu không có thông tin chính xác.",
        });
      } else {
        this.$store.dispatch("auth/login", this.form).then(
          () => {
            this.$router.push("/dashboard/default");
          },
          (error) => {
            this.message =
              (error.response && error.response.data) ||
              error.message ||
              error.toString();
          }
        );
      }

      /* let response = await axios.post(
        `http://localhost:9999/api/users/mst/login`,
        this.form
      ); */
      //console.log(response.data.user);
      //localStorage.setItem("user-token", response.data.token);
      //this.$store.dispatch("user", response.data.user);

      /* this.$router.push("/dashboard/default"); */
    },
  },
};
</script>